
.finished {
  text-decoration:line-through;
}
html {
  height: 100%;
}
body {
}
#app {
  color: #2c3e50;
  width: 650px;
  margin: 20px auto 0;
  font-family: '微软雅黑',Source Sans Pro, Helvetica, sans-serif;
  text-align: center;
  min-height: 500px;
}
.input-item {
  width: 300px;
  margin: 0 auto;
}
.input-item>div {
  margin: 10px;
  text-align: left;
}
input,select {
  display: inline-block;
  padding: 3px 10px;
  border-radius: 3px;
  border: 1px solid #42b983;
  outline: none;
}
label {
  display: inline-block;
  width: 50px;
  text-align: right;
}
th {
  background-color: #42b983;
  color: rgba(255,255,255,0.66);
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -user-select: none;
}
td {
  background-color: #f9f9f9;
}
th, td {
  min-width: 120px;
  padding: 10px 20px;
}
button {
  outline: none;
  padding: 5px 10px;
  border-radius: 4px;
  background-color: #42b983;
  border:none;
  cursor: pointer;
  color: rgba(255,255,255,0.66);
}
.console {
  display: none;
  position: absolute;
  top: 40px;
  left: 45%;
  padding: 10px;
  border-radius: 4px;
  background-color: #42b983;
  transition: all .5s;
  color: rgba(255,255,255,0.66);
}
.console.active {
  display: inline-block;
}




